
import common from '@/libs/common.mixin'
import Popover from 'vux/src/components/popover/index.vue'
import XCircle from 'vux/src/components/x-circle/index.vue'


export default{
mixins: [ common ],
components: {
XCircle,
Popover,
},
props: {
list: {
type: Array,
default: ()=>{
return []
}
},
disabled: {
type: Boolean,
default: false,
}
},
data () {
return {
all: {
is_in: false,
is_comsume: false,
is_leave: false
},
}
},
methods:{
leaveReasonList (item) {
let leave = this.dicts('leave_reason');
if(item.is_attendance==1){
return leave
}else{
return [{desc:'旷课'},{desc:'其他'}]
}
},
setStudentTag (item) {
let obj = {
color: 'blue',
label: '班'
}
if(item.is_trial==1){
obj.color = 'yellow'
obj.label = '试'
}
if(item.is_makeup==1){
obj.color = 'yellow'
obj.label = '补'
}
return obj
},
total (type) {
let list = this.list||[],
length = 0
if(type=='all'){
length = list.length
}else{
length = list.filter(l=>l[type]==1).length
}
return length
},
checkAll (field) {

if(!this.disabled){
this.all[field] = !this.all[field]
let val = this.all[field]
if(field=='is_leave'&&val){
this.all.is_in = false
}
if(field=='is_in'){
if(val) {
this.all.is_leave = false
}
this.all.is_consume = val
}

this.list.forEach(l=>{
if(!l.is_attendance && !l._disabled){
if((l.sid>0&&l.student.status==1)||l.cu_id>0){
l[field] = 	this.all[field]?1:0
this.itemChange(l[field],l,field)
}
}
})
}
},
itemStatusChange (item,field) {
if(item.is_attendance==0&&!this.disabled&&!item._disabled){
item[field] = item[field]==0?1:0
this.itemChange(item[field],item,field)
}
},
itemChange (val,item,field) {
if(field=='is_leave'&&val){
item.is_in = 0
}
if(field=='is_in'){
if(val) {

item.is_leave = 0
item.is_consume = 1
item.remark = ''
}else{
item.is_consume = 0
}
}
},
setStudentInfo (item,field) {
let obj = {}
if(item.sid>0){
obj = item.student||{}
obj.name = item.student?item.student.student_name:''
}else{
obj = item.customer||{}
}

return obj
},
}
}



