import Vue from 'vue'
import util from '@/libs/util'
import {_} from '@/libs/util'

const info = {
	lesson_type: 0,
	teach_eid: 0,
	lesson_remark: '',					
	students: [],
	is_push: true
}

export default{
	data () {
		return {			
			post: util.copy(info),
			id: this.$route.params.id,
			attendance_way: {0:'已考勤',1:'已刷卡',2:'已考勤'},
			
		}
	},
	computed: {
		attendStudents () {
			let filter_condition = (item) => {
				if(item.student_type=='arrange_students'){
					return !item.is_attendance && !item.is_leave && !item.is_suspend && !item.is_lost && (item.student_lesson!=null && item.student_lesson.remain_lesson_hours>0)					
				}else if(item.student_type=='trial_students'){
					return true
				}else{
					return true	
				}
			}
			return this.data.filter(filter_condition)
		},
	},
	methods: {
		handleSubmit () {
			if(this.initPost()){
				this.$rest('course_arranges/'+this.id+'/attendances')
				.post(this.post)
				.success(data=>{
					this.toast('考勤成功')
					this.$router.go(-1)					
				}).error(response=>{
					this.toast(response.body.message||'登记考勤失败','error')
				})
			}
		},		
		initPost () {
			let filter_condition = (item) => {
					if(item.student_type=='trial_students'){
						return true
					}else{
						if(!_.isEmpty(item.pivot)&&item.pivot.cid){
							return !item.is_attendance && !item.is_leave && !item.is_lost && item.status===1 && item.pivot.status===1 &&item.pivot.is_end===0						
						}else{
							return !item.is_attendance && !item.is_leave && !item.is_lost && item.status===1
						}
					}						
				},
				filter_condition2 = (item) => {
					return item.student_lesson&&item.student_lesson.remain_lesson_hours==0
				}
			
			let data = this.data.filter(filter_condition)
			let noLessonHourStudents = data.filter(filter_condition2)
						
			this.post.teach_eid = this.info.teach_eid
			this.post.lesson_type = this.info.lesson_type
						
			if(data.length>0){
				this.post.students = []
				data.forEach(d=>{	
					if(d.attendance_status){
						let obj = {
							sid: d.sid,
							is_consume: d.is_consume,
							attendance_status: d.attendance_status,
							remark: d.attendance_status===1?'':d.remark
						}
						if(d.student_type=='trial_students'){
							obj.is_trial = true
							
							if(d.is_student===0){
								Vue.delete(obj,'sid')
								obj.cu_id = d.cu_id
							}
						}else if(d.student_type=='makeup_students'){
							if(d.sa_id){
								obj.sa_id = d.sa_id
							}
							obj.is_makeup = true
						}
						
						this.post.students.push(obj)
					}
				})
				if(this.post.students.length===0){
					this.toast('请先选择学员','warn')
					return false
				}
								
				return true
			}else{
				this.toast('没有可考勤的学员','warn')
				return false
			}
		},	
		
		set (key,value,copy) {
			copy = copy || true
            if(copy){
                this.$set(this,key,util.copy(value))
            }else{
                this.$set(this,key,value)
            }
            return this
		},		
	}
	
}



